div.Education {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 16px;
  margin-left: 10px;
  color: var(--color-white-primary);

  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: -4px;
    transform: translate(-100%, -50%);
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: var(--color-white-primary);
  }

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: -6px;
    width: 2px;
    height: calc(50% + 2px);
    transform: translate(-100%, 0);
    border-radius: 2px;
    background-color: var(--color-white-primary);
  }
  
  + div.Education {
    &:after {
      height: calc(100% + 16px + 2px);
      transform: translate(-100%, calc(-50% - 8px));
    }
  }

  .date {
    font-size: 12px;
    margin-right: 8px;
    margin-bottom: 4px;
  }

  p {
    font-size: 16px;
    span { margin-left: 2px; }
  }
  
  @media screen and (max-width: 1280px) {
  }

  @media screen and (max-width: 1080px) {
  }

  @media screen and (max-width: 720px) {
    margin-bottom: 16px;
    .date { margin-bottom: 4px; }
    p { font-size: 14px; }
  }
}