#index {
  width: 100%;
  height: 100vh;

  display: flex;
  flex-direction: row;

  @media screen and (max-width: 1080px) {
    flex-direction: column;
  }
}

#index .left {
  position: relative;
  padding: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 320px;
  height: 100%;
  background-color: var(--color-dark);
  transition: width ease 0.4s;

  &.a {
    width: 30%;
  }

  .picture {
    width: 128px;
    height: 128px;
    border: 4px solid var(--color-white);
    border-radius: 50%;

    background-color: var(--color-white);
    overflow: hidden;
    margin-bottom: 24px;
  }

  h1 {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 8px;
    color: rgba($color: var(--color-white-rgb), $alpha: 0.6);
  }

  h2 {
    font-size: 24px;
    font-weight: normal;
    margin-bottom: 8px;
    color: var(--color-white);
  }

  h3 {
    margin-bottom: 24px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    font-size: 16px;
    font-weight: normal;
    color: rgba($color: var(--color-white-rgb), $alpha: 0.6);
  }

  .icons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    font-size: 32px;

    a {
      color: #3DC1D3;
      text-decoration: none;
      transform: scale(1);
      transition: transform ease 0.3s;

      &:hover {
        color: #f19066;
        transform: scale(1.4);
      }

      + a {
        margin-left: 8px;
      }
    }
  }

  @media screen and (min-width: 1280px) {
    width: 50%;
  }

  @media screen and (max-width: 1080px) {
    width: 100%;
    max-width: none;
    height: auto;
    padding: 32px 16px;
    align-items: center;

    &.a {
      width: 100%;
    }
  }
}

#index .right {
  padding: 32px;
  flex: 1;
  overflow-y: scroll;

  
  .techs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    width: 80%;
    margin: 0 auto 16px;

    > img {
      margin: 2px;
      width: 36px;

      &:hover {
        transform: rotate(10deg);
      }
    }
  }

  .wip {
    font-size: 12px;
    font-weight: bold;
    text-transform: uppercase;
    color: rgba($color: #000000, $alpha: 0.3);
    width: 576px;
    text-align: center;
    span {
      color: rgba($color: var(--color-white-primary-rgb), $alpha: 0.3);
    }
  }

  @media screen and (max-width: 1280px) {
    .wip {
      width: 80%;
      margin: 0 auto 16px;
    }
  }

  @media screen and (max-width: 1080px) {
    width: 100%;
    height: auto;
    padding: 32px 16px;
    overflow-y: unset;
  }

  @media screen and (max-width: 720px) {
    .techs {
      width: 70%;
      margin: 0 auto;
  
      > img {
        margin: 2px;
        flex: 20%;
      }
    }
  }
}