details.Collapse {
  margin-bottom: 16px;
  width: 576px;
  border-radius: 4px;
  box-shadow: rgba($color: #000, $alpha: 0.2) 0 2px 4px;

  background-color: rgba($color: #FFF, $alpha: 0.4);
  -webkit-transition: all .6s ease;
  -moz-transition: all .6s ease;
  -o-transition: all .6s ease;
  transition: all .6s ease;

  summary {
    padding: 8px 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: var(--color-dark);

    h3 {
      font-size: 24px;
      font-weight: bold;
      text-transform: capitalize;
    }

    i::before {
      content: '\e804'; // >
    }

    &:focus {
      outline-style: none;
    }

    &::-webkit-details-marker {
      display: none;
    }
  }

  &[open] {
    summary {
      i::before {
        content: '\e801'; // v
      }
    }
    
    background-color: rgba($color: #FFF, $alpha: 0.8);
    box-shadow: rgba($color: #000, $alpha: 0.3) 0 6px 8px;
  }

  > .content {
    display: flex;
    flex-direction: column;
    padding: 8px 16px;
  }

  &:not([open]):hover {
    background-color: rgba($color: #FFF, $alpha: 0.6);
    box-shadow: rgba($color: #000, $alpha: 0.2) 0 4px 8px;
  }

  @media screen and (max-width: 1280px) {
    width: 80%;
    margin: 0 auto 16px !important;
  }

  @media screen and (max-width: 1080px) {
    margin: 0 auto 16px !important;
  }

  @media screen and (max-width: 720px) {
    max-width: none;
    width: 100%;
  }
}