section.Section {
  width: 100%;
  margin-bottom: 16px;

  h4 {
    font-size: 18px;
    font-weight: 500;
    text-transform: capitalize;
    color: var(--color-dark);
    margin-bottom: 8px;
  }

  .content {
    p {
      font-size: 14px;
      color: var(--color-white-primary);

      + p {
        margin-top: 4px;
      }
    }
  }
}