div.Project {
  width: 100%;
  margin-bottom: 8px;

  + div.Project {
    padding: 8px 0 0;
    border-top: 1px solid var(--color-dark);
    margin-top: 8px;
  }

  h4 {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 16px;
    color: var(--color-dark);
  }

  > .content {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 16px;

    img {
      flex: none;
      width: 96px;
      height: auto;
      margin-right: 16px;
    }

    p {
      font-size: 14px;
      color: var(--color-white-primary);
      font-weight: normal;
    }
  }

  > .links {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;

    a {
      color: var(--color-dark-hover);
      font-weight: bold;
    }
  }
}