div.Lang {
  position: absolute;
  top: 16px;
  right: 16px;

  padding: 4px;
  display: flex;
  flex-direction: row;

  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 2px 4px rgba($color: #000000, $alpha: 0.2);

  span {
    padding: 4px;
    position: relative;

    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    color: var(--color-dark);

    border: none;
    background-color: none;
    transform: scale(1);

    &.active {
      color: var(--color-white-primary);
    }

    &:hover {
      color: var(--color-white-primary);
      text-decoration: underline var(--color-white-primary);
    }

    + span {
      margin-left: 9px;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -5px;
        width: 1px;
        height: 100%;
        background-color: var(--color-dark);
      }
    }
  }

  @media screen and (max-width: 1080px) {
    top: unset;
    right: unset;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%);
  }
}